import Captcha from './Captcha.vue';
import toastr from 'toastr';
import TinyPagination from './pagging/opencps_pagination.vue';
toastr.options = {
  'closeButton': true,
  'timeOut': '3000'
};
export default {
  props: ['serviceCode'],
  components: {
    'captcha': Captcha,
    'tiny-pagination': TinyPagination
  },
  data: function data() {
    return {
      dialog_captcha: false,
      validCaptcha: false,
      govAgencyList: [],
      govAgencyFilter: '',
      domainList: [],
      domainListRender: [],
      panelDomainList: [true],
      domainFilter: '',
      panelAgency: [],
      panelDomain: [],
      govAgencies: [],
      govAgencyFilterList: [],
      govAgencyRender: [],
      serviceOptions: [],
      serviceConfigSelect: '',
      serviceConfigList: [],
      serviceConfigListRender: [],
      serviceInfoIdSelect: '',
      serviceOptionsProcess: [],
      selectOption: false,
      govAgencyCodeSelect: '',
      serviceNameKey: '',
      activeFilterKey: false,
      loadingMutiple: true,
      dataPostDossier: '',
      serviceLastest: false,
      numberPerPage: 10,
      agencyPage: 1,
      dialogVerifycation: false,
      verificationApplicantCreateDossier: false,
      hasVerify: false,
      dialogLoadingCreate: true,
      dialog_selectOption: true
    };
  },
  computed: {
    currentIndex: function currentIndex() {
      return this.$store.getters.index;
    },
    currentUser: function currentUser() {
      return this.$store.getters.loadingInitData.user;
    },
    userLoginInfomation: function userLoginInfomation() {
      return this.$store.getters.getUserLogin;
    },
    isMobile: function isMobile() {
      return this.$store.getters.getIsMobile;
    }
  },
  created: function created() {
    var vm = this; //

    try {
      vm.verificationApplicantCreateDossier = hasVerificationCreateDossier;
    } catch (error) {} // 


    var current = vm.$router.history.current;
    var newQuery = current.query;
    vm.$store.dispatch('loadServiceConfigsGov', {}).then(function (result) {
      vm.govAgencies = result;
      vm.govAgencyFilterList = vm.govAgencies;
      vm.govAgencyList = [];
      vm.domainList = [];
      vm.serviceConfigList = [];

      for (var index in vm.govAgencies) {
        var item = {
          govAgencyCode: vm.govAgencies[index]['govAgencyCode'],
          govAgencyName: vm.govAgencies[index]['govAgencyName']
        };
        vm.govAgencyList.push(item);
        var itemDomain = vm.govAgencies[index]['domains'];

        for (var index2 in itemDomain) {
          var item2 = {
            govAgencyCode: vm.govAgencies[index]['govAgencyCode'],
            govAgencyName: vm.govAgencies[index]['govAgencyName'],
            domainCode: itemDomain[index2]['domainCode'],
            domainName: itemDomain[index2]['domainName'],
            serviceConfigs: itemDomain[index2]['serviceConfigs']
          };
          vm.domainList.push(item2);
          var itemServiceConfig = vm.govAgencies[index]['domains'][index2]['serviceConfigs'];

          for (var index3 in itemServiceConfig) {
            var item3 = {
              level: itemServiceConfig[index3]['level'],
              serviceConfigId: itemServiceConfig[index3]['serviceConfigId'],
              serviceInfoId: itemServiceConfig[index3]['serviceInfoId'],
              serviceInfoName: itemServiceConfig[index3]['serviceInfoName'],
              govAgencyCode: vm.govAgencies[index]['govAgencyCode']
            };
            vm.serviceConfigList.push(item3);
          }
        }
      }

      vm.serviceConfigListRender = vm.serviceConfigList;
      vm.domainListRender = vm.domainList; // 

      if (newQuery['domain']) {
        vm.domainFilter = newQuery['domain'];
      }

      if (newQuery['agency']) {
        vm.govAgencyFilter = newQuery['agency'];
      }

      if (String(vm.serviceCode) === '0') {
        vm.filterAndSort();
      } // 


      if (String(vm.serviceCode) !== '0') {
        for (var key in vm.govAgencies) {
          var govAgencyCode = vm.govAgencies[key].govAgencyCode;

          for (var keyDomain in vm.govAgencies[key].domains) {
            for (var keyConfig in vm.govAgencies[key].domains[keyDomain].serviceConfigs) {
              var serviceConfig = vm.govAgencies[key].domains[keyDomain].serviceConfigs[keyConfig];
              var serviceConfigId = serviceConfig.serviceConfigId;

              if (String(vm.serviceCode) === String(serviceConfigId)) {
                vm.govAgencyCodeSelect = govAgencyCode;
                vm.serviceInfoIdSelect = serviceConfig.serviceInfoId;
                vm.pullServiceOptions(serviceConfig, govAgencyCode);
                break;
              }
            }
          }
        }
      }
    }).catch(function () {});
  },
  updated: function updated() {},
  watch: {
    '$route': function $route(newRoute, oldRoute) {
      var vm = this;
      vm.filterAndSort();
    },
    serviceLastest: function serviceLastest(val) {
      var vm = this;

      if (val) {
        vm.govAgencyFilter = '';
        vm.domainFilter = '';
        vm.serviceNameKey = '';
        vm.filterService();
      } else {
        vm.filterService();
      }
    }
  },
  methods: {
    changeFilterAgency: function changeFilterAgency() {
      var vm = this;
      vm.domainFilter = '';
      vm.serviceNameKey = '';
      vm.activeFilterKey = false;
      vm.serviceLastest = false;
      vm.filterService();
    },
    changeFilterDomain: function changeFilterDomain() {
      var vm = this;
      vm.govAgencyFilter = '';
      vm.serviceNameKey = '';
      vm.activeFilterKey = false;
      vm.serviceLastest = false;
      vm.panelDomainList = [true];
      vm.filterService();
    },
    filterServiceName: function filterServiceName() {
      var vm = this;

      if (vm.serviceNameKey && vm.serviceNameKey.length > 5) {
        vm.govAgencyFilter = '';
        vm.domainFilter = '';
        vm.serviceLastest = false;
        vm.activeFilterKey = true;
        vm.filterService();
      } else {
        vm.govAgencyFilter = '';
        vm.domainFilter = '';
        vm.serviceNameKey = '';
        vm.serviceLastest = false;
        vm.activeFilterKey = false;
        vm.filterService();
      }
    },
    changePage: function changePage(config) {
      var vm = this;
      var current = vm.$router.history.current;
      var newQuery = current.query;
      var queryString = '?';
      vm.agencyPage = config.page;
      newQuery['page'] = '';

      for (var key in newQuery) {
        if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null && newQuery[key] !== 'null') {
          queryString += key + '=' + newQuery[key] + '&';
        }
      }

      queryString += 'page=' + vm.agencyPage;
      vm.$router.push({
        path: current.path + queryString,
        query: {
          renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
        }
      });
    },
    filterService: function filterService() {
      var vm = this;
      setTimeout(function () {
        var current = vm.$router.history.current;
        var newQuery = current.query;
        var queryString = '?';
        newQuery['agency'] = vm.govAgencyFilter;
        newQuery['domain'] = vm.domainFilter;

        if (vm.serviceLastest) {
          newQuery['lastest'] = vm.serviceLastest;
        } else {
          newQuery['lastest'] = '';
        }

        newQuery['keyword'] = String(vm.serviceNameKey).trim();

        if (!vm.govAgencyFilter) {
          newQuery['page'] = 1;
        }

        for (var key in newQuery) {
          if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null) {
            queryString += key + '=' + newQuery[key] + '&';
          }
        }

        vm.$router.push({
          path: current.path + queryString,
          query: {
            renew: Math.floor(Math.random() * (100 - 1 + 1)) + 1
          }
        });
      }, 5);
    },
    filterAndSort: function filterAndSort() {
      var vm = this;
      vm.govAgencyRender = [];
      vm.govAgencyRender = [];
      vm.domainListRender = [];
      var current = vm.$router.history.current;
      var newQuery = current.query;

      if (!newQuery.hasOwnProperty('agency') && !newQuery.hasOwnProperty('domain') && !newQuery.hasOwnProperty('keyword') && !newQuery['lastest']) {
        vm.govAgencyFilterList = vm.govAgencies;

        if (newQuery.hasOwnProperty('page') && newQuery['page']) {
          vm.agencyPage = newQuery['page'];
          var start = vm.agencyPage * vm.numberPerPage - vm.numberPerPage;
          var end = vm.agencyPage * vm.numberPerPage;
          vm.govAgencyRender = vm.govAgencyFilterList.slice(start, end);
        } else {
          vm.agencyPage = 1;

          var _start = vm.agencyPage * vm.numberPerPage - vm.numberPerPage;

          var _end = vm.agencyPage * vm.numberPerPage;

          vm.govAgencyRender = vm.govAgencyFilterList.slice(_start, _end);
        }
      }

      if (newQuery.hasOwnProperty('agency') && newQuery.agency) {
        vm.govAgencyRender = vm.govAgencies.filter(function (item) {
          return String(item['govAgencyCode']) === newQuery.agency;
        });
      }

      if (newQuery.hasOwnProperty('domain') && newQuery.domain) {
        vm.domainListRender = vm.domainList.filter(function (item) {
          return String(item['domainCode']) === newQuery.domain;
        });
      }

      if (newQuery.hasOwnProperty('keyword') && newQuery.keyword) {
        var keySearch = vm.convertString(newQuery.keyword);
        vm.serviceConfigListRender = vm.serviceConfigList.filter(function (item) {
          return vm.convertString(String(item['serviceInfoName'])).indexOf(keySearch) >= 0;
        });
      }

      if (newQuery.hasOwnProperty('lastest') && newQuery.lastest && String(newQuery.lastest) !== 'false') {
        vm.$store.dispatch('getServiceRecently').then(function (result) {
          if (result.length > 0) {
            var serviceConfigs = [];

            for (var index in result) {
              if (Array.isArray(result[index]['serviceConfigs'])) {
                for (var key in result[index]['serviceConfigs']) {
                  result[index]['serviceConfigs'][key].serviceInfoId = result[index]['serviceConfigs'][key]['serviceInfoId'];
                  result[index]['serviceConfigs'][key].serviceInfoName = result[index]['serviceName'];
                  result[index]['serviceConfigs'][key].govAgencyNameRender = result[index]['serviceConfigs'][key]['govAgencyName'];
                  serviceConfigs.push(result[index]['serviceConfigs'][key]);
                }
              } else {
                result[index]['serviceConfigs'].serviceInfoId = result[index]['serviceInfoId'];
                result[index]['serviceConfigs'].serviceInfoName = result[index]['serviceName'];
                serviceConfigs.push(result[index]['serviceConfigs']);
              }
            }

            vm.serviceConfigListRender = serviceConfigs;
          } else {
            vm.serviceConfigListRender = [];
          }
        }).catch(function () {
          vm.serviceConfigListRender = [];
        });
      }

      if (!vm.domainFilter && !vm.serviceNameKey) {
        if (vm.govAgencyRender.length === 1) {
          vm.panelAgency = [true];

          if (vm.govAgencyRender[0].domains.length === 1) {
            vm.panelDomain = [];
            vm.panelDomain[0] = [true];
          } else {
            for (var key in vm.govAgencyRender[0].domains) {
              vm.panelDomain.push([]);
              vm.panelDomain[key].push(false);
            }

            vm.panelDomain[0] = [true];
          }
        } else {
          for (var _key in vm.govAgencyRender) {
            vm.panelAgency.push(false);
            vm.panelDomain.push([]);

            if (vm.govAgencyRender[_key].domains.length === 1) {
              vm.panelDomain[0] = [true];
            } else {
              for (var index in vm.govAgencyRender[_key].domains[index]) {
                vm.panelDomain[_key].push(false);
              }
            }
          }
        }
      }
    },
    pullServiceOptions: function pullServiceOptions(item, govAgencyCode) {
      var vm = this;

      if (vm.verificationApplicantCreateDossier && vm.userLoginInfomation && vm.userLoginInfomation['verification'] && String(vm.userLoginInfomation['verification']) === '2') {
        vm.hasVerify = true;
        setTimeout(function () {
          vm.dialogVerifycation = true;
        }, 300);
      } else {
        vm.serviceConfigSelect = item;
        vm.govAgencyCodeSelect = govAgencyCode;
        vm.serviceInfoIdSelect = item.serviceInfoId;
        vm.$store.dispatch('getServiceOpionByProcess', item).then(function (result) {
          if (result) {
            vm.serviceOptions = result;

            if (result.length === 1) {
              vm.selectOption = false;
              vm.$store.dispatch('getServiceInfo', {
                serviceInfoId: item.serviceInfoId
              }).then(function (resServiceInfo) {
                var data = {
                  serviceCode: resServiceInfo.serviceCode,
                  govAgencyCode: govAgencyCode,
                  templateNo: result[0].templateNo,
                  originality: vm.getOriginality(),
                  j_captcha_response: ''
                };

                if (!vm.isOffLine) {
                  vm.trackingBTTT(resServiceInfo.serviceCode);
                  vm.$store.dispatch('postDossier', data).then(function (result) {
                    vm.loadingAction = false;
                    vm.indexAction = -1;
                    vm.$router.push({
                      path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
                      query: vm.$router.history.current.query
                    });
                  });
                } else {
                  vm.dataPostDossier = data;
                  vm.$refs.captcha.makeImageCap();
                  vm.dialog_captcha = true;
                }
              });
            } else {
              vm.serviceOptionsProcess = result;
              vm.selectOption = true;
            }
          } else {
            vm.$store.dispatch('getServiceConfigDetail', item).then(function (result) {
              if (result.hasOwnProperty('serviceUrl') && result.serviceUrl) {
                window.location.href = result.serviceUrl;
              }
            }).catch(function () {});
          }
        }).catch(function (result) {
          vm.serviceOptions = [];
        });
      }
    },
    closeSelectOption: function closeSelectOption() {
      var vm = this;
      vm.dialog_selectOption = false;
      vm.filterAndSort();
    },
    selectServiceOption: function selectServiceOption(item, govAgencyCode, itemServiceConfig) {
      var vm = this;
      vm.serviceConfigSelect = itemServiceConfig;
      vm.govAgencyCodeSelect = govAgencyCode;
      vm.serviceInfoIdSelect = itemServiceConfig.serviceInfoId;
      vm.loadingMutiple = true;
      vm.$store.dispatch('getServiceInfo', {
        serviceInfoId: vm.serviceConfigSelect.serviceInfoId
      }).then(function (resServiceInfo) {
        var data = {
          serviceCode: resServiceInfo.serviceCode,
          govAgencyCode: govAgencyCode,
          templateNo: item.templateNo,
          originality: vm.getOriginality(),
          j_captcha_response: ''
        };

        if (!vm.isOffLine) {
          vm.trackingBTTT(resServiceInfo.serviceCode);
          vm.$store.dispatch('postDossier', data).then(function (result) {
            vm.loadingAction = false;
            vm.indexAction = -1;
            vm.$router.push({
              path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
              query: vm.$router.history.current.query
            });
          });
        } else {
          vm.dataPostDossier = data;
          vm.$refs.captcha.makeImageCap();
          vm.dialog_captcha = true;
        }
      });
    },
    selectServiceOptionCRD: function selectServiceOptionCRD(item, govAgencyCode) {
      var vm = this;
      vm.$store.dispatch('getServiceInfo', {
        serviceInfoId: vm.serviceInfoIdSelect
      }).then(function (resServiceInfo) {
        vm.trackingBTTT(resServiceInfo.serviceCode);
        var data = {
          serviceCode: resServiceInfo.serviceCode,
          govAgencyCode: govAgencyCode,
          templateNo: item.templateNo,
          originality: vm.getOriginality()
        };
        vm.$store.dispatch('postDossier', data).then(function (result) {
          vm.loadingAction = false;
          vm.indexAction = -1;
          vm.$router.push({
            path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.dossierId + '/NEW',
            query: vm.$router.history.current.query
          });
        });
      });
    },
    doCreateDossier: function doCreateDossier() {
      var vm = this;
      vm.$refs.formCaptcha.resetValidation();
      var captchaInput = vm.$refs.captcha.j_captcha_response;

      if (vm.$refs.formCaptcha.validate()) {
        vm.dataPostDossier['j_captcha_response'] = captchaInput;
        vm.createDossier(vm.dataPostDossier);
      }
    },
    createDossier: function createDossier(data) {
      var vm = this;

      if (vm.serviceConfigSelect.serviceUrl) {
        window.location.href = vm.serviceConfigSelect;
      } else {
        vm.trackingBTTT(data.serviceCode);
        vm.$store.dispatch('postDossier', data).then(function (result) {
          if (result['status'] !== undefined && result['status'] === 203) {
            vm.loadingAction = false;
            toastr.clear();
            toastr.error('Mã captcha không chính xác. Vui lòng thử lại');
            vm.$refs.captcha.makeImageCap();
          } else {
            vm.loadingAction = false;
            vm.dialog_captcha = false;
            vm.indexAction = -1;
            vm.$router.push({
              path: '/danh-sach-ho-so/' + 0 + '/ho-so/' + result.data.dossierId + '/NEW',
              query: vm.$router.history.current.query
            });
          }
        }).catch(function (reject) {
          toastr.error('Nộp hồ sơ không thành công');
          vm.$refs.captcha.makeImageCap();
        });
      }
    },
    trackingBTTT: function trackingBTTT(serviceCode) {
      try {
        if (_govaq) {
          _govaq.push(['trackDVC', serviceCode, '1', '']);
        }
      } catch (error) {}
    },
    getColor: function getColor(level) {
      if (level === 2) {
        return 'green';
      } else if (level === 3) {
        return 'orange';
      } else if (level === 4) {
        return 'red';
      }
    },
    goBack: function goBack() {
      window.history.back();
    },
    convertString: function convertString(str) {
      str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, 'a');
      str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, 'e');
      str = str.replace(/ì|í|ị|ỉ|ĩ/g, 'i');
      str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, 'o');
      str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, 'u');
      str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, 'y');
      str = str.replace(/đ/g, 'd');
      str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, 'A');
      str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, 'E');
      str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, 'I');
      str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, 'O');
      str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, 'U');
      str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, 'Y');
      str = str.replace(/Đ/g, 'D');
      str = str.toLocaleLowerCase().replace(/\s/g, '');
      return str;
    }
  }
};