      var vm = this;
      var currentParams = vm.$router.history.current.params;
      var currentQuery = vm.$router.history.current.query;

      if (vm.isOffLine) {
        vm.$router.push({
          path: '/add-dvc/0'
        });
      } else {
        vm.$router.push({
          path: '/danh-sach-ho-so/' + currentParams.index,
          query: currentQuery
        });
      }
    },
    goBackHistory: function goBackHistory() {
      window.history.back();
    }
  }
};